*{padding:0;margin:0;list-style:none;outline:none;box-sizing:border-box}html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%;font-size:16px}@media (max-width: 768px){html{font-size:15px}}@media (max-width: 576px){html{font-size:14px}}body{font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";background-color:#1e1e1e;color:#90d9fe;min-height:100vh}a{text-decoration:none;color:currentColor}.wrapper{min-height:80vh;padding-top:70px}.wrapper.opacity{opacity:0.1;transition:1s}.contact-icon{width:30px}@media (max-width: 768px){.contact-icon{width:50px}}.itachi{position:absolute;z-index:99;top:0;left:0;width:100vw;height:100vh;object-fit:cover;opacity:0;transition:.5s}.itachi.visibility{opacity:1}.nav-active{opacity:0.4}.nav{padding:1rem;display:flex;justify-content:space-between;align-items:center;position:fixed;z-index:10;width:100%;font-size:1.5rem;top:0;left:0}.nav-open{transform:rotate(90deg);transition:0.5s}.nav-open.opacity{opacity:0}@media (max-width: 768px){.nav__wrapper{display:flex;align-items:flex-end;justify-content:flex-end;position:absolute;top:0;right:0;width:100vw;height:100vh;height:calc(var(--vh, 1vh) * 100);text-align:right;z-index:20;padding:8rem 1rem 1.5rem;transform:translate(100%);transition:1s cubic-bezier(0.86, 0, 0.07, 1)}.nav__wrapper.transform{transform:translate(0)}}.nav-close{position:absolute;top:0;right:5px;padding:1rem}.nav-list{display:flex;flex-wrap:wrap}@media (max-width: 768px){.nav-list{display:block;font-size:2.5rem}}.nav-list__link{line-height:1.2rem;margin:5px}@media (max-width: 768px){.nav-list__link{line-height:3.2rem}.nav-list__link.icon{display:inline-block;margin:10px}}.nav-list__link.space{margin:5px 15px}
